<template>
	<m-container header>
		<m-header fixed>
			<div slot="center">
				<span></span>
			</div>
		</m-header>
		<m-card :title="false">
			<template slot="content"></template>
		</m-card>
		<mt-cell title="">
			<mt-switch v-model="timeNow"></mt-switch>
		</mt-cell>
		<mt-cell v-show="!timeNow" title="" is-link @click.native="openPicker" :value="timeOthers&&timeOthers.format('yyyy-MM-dd hh:mm')"></mt-cell>
		
		<mt-field label="" placeholder="input your other required" type="textarea" rows="3" v-model="note"></mt-field>
		<div style="padding:30px 20px;">
			<mt-button type="primary" style="width:100%" @click="order"></mt-button>
		</div>
		<mt-datetime-picker ref="picker" @confirm="timeConfirm" type="datetime" year-format="{value}" month-format="{value} " date-format="{value} " hour-format="{value} " minute-format="{value} " :startDate="new Date(new Date().setHours(0,0,0,0))" :endDate="new Date(new Date().getTime() + 1000*60*60*24*7)" v-model="pickerValue">
		</mt-datetime-picker>
		<mt-popup v-model="popupVisible" position="bottom">
			<mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
		</mt-popup>
		
	</m-container>
</template>

<script>
export default {
	data() {
		return {
			timeNow: true,
			timeOthers: null,
			pickerValue: '',
			note: '',
			popupVisible:false,
			slots:[{
				value:['','','','']
			}]
		}
	},
	methods: {
		openPicker() {
			this.$refs.picker.open();
		},
		timeConfirm(time) {
			this.timeOthers = time
		},
		others() {
			this.popupVisible = true
		},
		order() {
			this.$messagebox({
				title: '',
				message: ''
			});
		},
		onValuesChange(){

		}
	}
}
</script>

<style scoped>

</style>



