import Vue from 'vue'
import Router from 'vue-router'
import index from '@/page/index'//+ 
import home from '@/page/home'//
import lecture from '@/page/lecture'//
import teacher from '@/page/teacher'//
import usercenter from '@/page/usercenter'//
/** */
import lecture_detail from '@/page/lecture-detail'//
import teacher_detail from '@/page/teacher-detail'//
import teacher_quickOrder from '@/page/teacher-quickOrder'//

/** */
import lecture_detail_online from '@/page/lecture-detail-online'//

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      component: index,
      children: [{
        path: 'home',
        component: home
      }, {
        path: 'lecture',
        component: lecture
      }, {
        path: 'teacher',
        component: teacher
      }, {
        path: 'usercenter',
        component: usercenter
      }]
    },
    {
      path: '/lecture/detail',
      component: lecture_detail
    },{
      path: '/teacher/detail',
      component: teacher_detail
    },
    {
      path: '/lecture/detail/online',
      component: lecture_detail_online
    },
    {
      path: '/lecture/teacher/quickOrder',
      component: teacher_quickOrder
    }
  ]
})



